import React from 'react';

const Button = (props) => {
  return (
    <button onClick={props.getQuote} style={{ marginTop: 1 + 'em', color: 'white', backgroundColor: '#1DA1F2', border: 'none', paddingTop: '0.3em', paddingBottom: '0.3em', fontSize: '1.2em', outline: 'none' }}>Get Quote</button>
  );
}

export default Button;


