import React, { Component } from 'react';
import axios from "axios";
import './App.css';


import Button from "./components/Button";

class App extends Component {
  state = {
    quote: null
  }
  getQuote = (e) => {
      axios.get(`https://api.kanye.rest/`)
      .then((res) => {
        const quote = res.data.quote;
        console.log(quote);
        this.setState({ quote });
      })
  }
  render() {
    return (
      <div className="App">
        <header className="App-header" style={{backgroundColor: '#1DA1F2'}}>
          <h1 className="App-title">Kanye quotes</h1>
        </header>
        <Button getQuote={this.getQuote} />
        { this.state.quote ? <p style={{fontSize: '1.2em'}}>"{ this.state.quote }"</p> : <p style={{fontSize: '1.2em'}}>Click the button for a quote</p> }
      </div>
    );
  }
};

export default App;



